.main-banner {
	height: 715px;
	position: relative;
	padding: 70px 0 0;
	@include vp-1439 {
		height: 615px;
	}
	@include vp-767 {
		height: 640px;
		padding: 110px 0;
	}
	&__container {
		position: relative;
	}
	&__pic {
		width: 100%;
		height: calc(100% - 51px);
		border-radius: 50% / 77px;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		overflow: hidden;
		position: absolute;
		top: 41px;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
		@include vp-767 {
			border-radius: 0;
			height: unset;
			top: 110px;
			bottom: 110px;
		}
		@include vp-425 {
			top: 110px;
		}
	}
	&__img {
		height: inherit;
		@include vp-767 {
			img {
				object-position: top;
			}
		}
		&_mobile {
			display: none;
			@include vp-425 {
				display: block;
			}
		}
		&_desktop {
			@include vp-425 {
				display: none;
			}
		}
	}
	&__heading {
		margin: 184px 0 0 100px;
		@include vp-1439 {
			margin: 84px 0 0 100px;
		}
		@include vp-767 {
			margin: 1.5em 0 0 2em;
		}
		@include vp-425 {
			margin: 0;
			padding: 20px 0 0;
		}
	}
	&__title {
		margin: 0;
		padding: 0;
		color: $color-curious-blue;
		max-width: 339px;
		&-span {
			&-big {
				display: block;
				font-family: $ff-lora;
				font-weight: bold;
				font-size: 46px;
				line-height: 60px;
				letter-spacing: 0.1em;
				text-transform: uppercase;
				text-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
				margin-bottom: 22px;
				@include vp-767 {
					line-height: 50px;
					margin-bottom: 20px;
				}
			}
			&-small {
				font-weight: 600;
				font-size: 20px;
				line-height: 20px;
				text-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
			}
		}
		@include vp-425 {
			text-align: center;
		}
	}
	&__btn {
		padding: 11px 40px;
		background: $curious-blue-to-picton-blue;
		border-radius: 5px;
		font-weight: bold;
		font-size: 16px;
		line-height: 150%;
		margin: 82px 0 0;
		@include vp-767 {
			position: absolute;
			top: 441px;
			margin: 0;
			max-width: calc(100% - 2 * 20px);
			width: 100%;
			justify-content: center;
			align-items: center;
			left: 50%;
			transform: translateX(-50%);
		}
	}
}
