/* lora-regular - latin_cyrillic */
@font-face {
  font-family: "Lora";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lora-v23-latin_cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local(""), url("../fonts/lora-v23-latin_cyrillic-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/lora-v23-latin_cyrillic-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("../fonts/lora-v23-latin_cyrillic-regular.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/lora-v23-latin_cyrillic-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("../fonts/lora-v23-latin_cyrillic-regular.svg#Lora") format("svg"); /* Legacy iOS */
}
